:root {
  --elevation-3: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
  --elevation-1: 0 0 10px 0 rgba(0, 0, 0, 0.1), 0 5px 5px 0 rgba(0, 0, 0, 0.12);
}

.codebox {
  border: 0.5em solid rgb(0, 0, 0, 0);
  padding: 25px 10px !important;
  border-radius: 35px !important;
  box-sizing: border-box;
  overflow: auto;
  box-shadow: var(--elevation-3);
  width: 100% !important;
  height: 93%;
  font-size: 0.7rem !important;
}

.codebox:hover {
  border: 0.5em solid rgb(0, 0, 0, 1);
}

.codebox::-webkit-scrollbar {
  width: 1em !important;
}

.codebox::-webkit-scrollbar-track {
  background: rgb(255, 255, 255) !important;
  border-radius: 35px !important;
  margin-block: 1.3em !important;
}

.codebox::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.418) !important;
  border-radius: 35px !important;
  height: 1em !important;
}

.codebox::-webkit-scrollbar-thumb:hover {
  background: rgb(71, 71, 71) !important;
}

@media screen and (min-width: 781px) {
  .codebox {
    height: 100% !important;
    font-size: 1rem !important;
  }
}